import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import './Header.css';

const Header = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen);
  };

  const closeMenu = () => {
    setIsMenuOpen(false);
  };

  return (
    <header className="header">
      <div className="logo">
        <Link to="/"> </Link>
      </div>
      <button className="hamburger" onClick={toggleMenu} aria-label=" ">
        
      </button>
      <nav className={`nav-menu ${isMenuOpen ? 'active' : ''}`}>
        <ul className="nav-list">
          <li className="nav-item">
            <Link to="/about" onClick={closeMenu} className="nav-link">
              
            </Link>
          </li>
          <li className="nav-item">
            <Link to="/write-intro" onClick={closeMenu} className="nav-link">
               
            </Link>
          </li>
          <li className="nav-item">
            <Link to="/login" onClick={closeMenu} className="nav-link">
              
            </Link>
          </li>
        </ul>
      </nav>
    </header>
  );
};

export default Header;
