import React, { useState } from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import Header from './components/Header';
import Calendar from './components/Calendar';
import Modal from './components/Modal';
import UploadResume from './components/UploadResume'; //    
import QuestionMatch from './components/QuestionMatch'; //    
import WriteIntro from './components/WriteIntro'; //    

const App = () => {
  //    
  const [selectedCompany, setSelectedCompany] = useState(null);

  //    
  const [favorites, setFavorites] = useState([]);

  //   
  const companies = [
    {
      name: '',
      position: ' ',
      deadline: '2024-12-31',
      description: '  IT     .',
    },
    {
      name: '',
      position: ' ',
      deadline: '2024-12-25',
      description: '       IT  .',
    },
    {
      name: '',
      position: ' ',
      deadline: '2024-12-28',
      description: '        IT .',
    },
    {
      name: 'LG',
      position: ' ',
      deadline: '2024-12-28',
      description: 'LG        IT .',
    },
  ];

  //     
  const handleCompanyClick = (companyName) => {
    const company = companies.find((comp) => comp.name === companyName);
    setSelectedCompany(company || null);
  };

  //   /
  const toggleFavorite = (companyName) => {
    const company = companies.find((comp) => comp.name === companyName);
    if (!company) return;

    if (favorites.some((fav) => fav.name === company.name)) {
      //   
      setFavorites(favorites.filter((fav) => fav.name !== company.name));
    } else {
      //   
      setFavorites([...favorites, company]);
    }
  };

  //  
  const closeModal = () => {
    setSelectedCompany(null);
  };

  return (
    <Router>
      <Header />
      <Routes>
        <Route
          path="/"
          element={
            <>
              {/*      */}
              <Calendar
                onCompanyClick={handleCompanyClick}
                onToggleFavorite={toggleFavorite}
                favorites={favorites}
              />
              {selectedCompany && (
                <Modal onClose={closeModal} company={selectedCompany} />
              )}
              {/*    */}
              <div className="favorites">
                 <h3> </h3>
                <ul>
                  {favorites.length > 0 ? (
                    favorites.map((company, index) => (
                      <li key={index}>
                        <button
                          onClick={() => handleCompanyClick(company.name)}
                        >
                          {company.name}
                        </button>
                      </li>
                    ))
                  ) : (
                    <p>   .</p>
                  )}
                </ul>
              </div>
            </>
          }
        />
        <Route path="/about" element={<h1> </h1>} />
        <Route path="/write-intro" element={<WriteIntro />} />
        <Route path="/login" element={<h1> </h1>} />
        <Route path="/upload-resume" element={<UploadResume />} /> {/*      */}
        <Route
          path="/match-questions"
          element={
            <QuestionMatch
              questions={[
                '   .',
                '   .',
              ]}
              starData={[
                {
                  situation: '     .',
                  task: '   .',
                  action: '        .',
                  result: '    .',
                },
                {
                  situation: '       .',
                  task: '   .',
                  action: '       .',
                  result: '  .',
                },
              ]} //  
            />
          }
        />
      </Routes>
    </Router>
  );
};

export default App;
